<template>
  <v-app id="keep">
    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click="drawer = !drawer" />
      <v-toolbar-title class="secondary--text">
        Tarkov Resource Planner v{{version}}
        <span class="tiny">for Tarkov v0.12.3</span>
      </v-toolbar-title>
      <v-spacer />
      <v-btn :color="this.$store.state.dirty?'primary':''" @click="save">Save to cookie! ({{ago}})</v-btn>
      <v-snackbar v-model="snackbar" :timeout="1000">
        Saved to cookie!
        <v-btn color="pink" text @click="snackbar = false">Close</v-btn>
      </v-snackbar>
    </v-app-bar>

    <v-navigation-drawer v-model="drawer" app clipped>
      <div class="flex-col h100">
        <div class="flex-grow">
          <v-list-item to="/info">
            <v-list-item-action>
              <v-icon>info</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Info</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/assistent">
            <v-list-item-action>
              <v-icon>account_circle</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Setup Assistent</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item></v-list-item>
          <v-divider></v-divider>
          <v-list-item></v-list-item>
          <v-list-item to="/character">
            <v-list-item-action>
              <v-icon>account_circle</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Character</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/quests">
            <v-list-item-action>
              <v-icon>playlist_add_check</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Quests</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/hideout">
            <v-list-item-action>
              <v-icon>playlist_add_check</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Hideout</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/checklist">
            <v-list-item-action>
              <v-icon>check_box</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Checklist</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item></v-list-item>
          <v-divider></v-divider>
          <v-list-item></v-list-item>
          <v-list-item to="/port">
            <v-list-item-action>
              <v-icon>vertical_align_center</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Import/Export</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/compare">
            <v-list-item-action>
              <v-icon>group</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Quest comparator</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
        <div class="flex-grow h100"></div>
        <div class="flex-shrink">
          <v-list-item>
            <v-list-item-action></v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>by Astarch</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
      </div>
    </v-navigation-drawer>

    <v-content>
      <v-container align-start fluid>
        <keep-alive>
          <router-view @save="save"></router-view>
        </keep-alive>
      </v-container>
    </v-content>
  </v-app>
</template>
<script>
import { version } from "../package.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      version,
      drawer: true,
      snackbar: false,
      tab: null,
      lastSave: new Date(),
      items: ["Quests", "Hideout", "Stocked", "Info"],
      text:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat."
    };
  },
  computed: {
    ago: function() {
      return this.lastSave.toLocaleTimeString();
    }
  },
  methods: {
    save() {
      const year = 60 * 60 * 24 * 365 + "s";
      this.lastSave = new Date();
      console.log(this.$store.state);
      this.$cookies.set(
        "doneQuests",
        JSON.stringify(this.$store.state.doneQuests),
        year
      );
      this.$cookies.set(
        "doneHideout",
        JSON.stringify(this.$store.state.doneHideout),
        year
      );
      this.$cookies.set(
        "stocked",
        JSON.stringify(this.$store.state.stocked),
        year
      );
      this.$cookies.set(
        "config",
        JSON.stringify(this.$store.state.config),
        year
      );
      this.$cookies.set(
        "levels",
        JSON.stringify(this.$store.state.levels),
        year
      );
      this.$cookies.set("money", JSON.stringify(this.$store.state.money), year);
      this.$cookies.set(
        "loyalties",
        JSON.stringify(this.$store.state.loyalties),
        year
      );
      this.$store.commit("setDirty", false);
      this.snackbar = true;
    }
  },
  created() {
    var doneQuests = this.$cookies.get("doneQuests");
    var doneHideout = this.$cookies.get("doneHideout");
    var stocked = this.$cookies.get("stocked");
    var config = this.$cookies.get("config");
    var levels = this.$cookies.get("levels");
    var money = this.$cookies.get("money");
    var loyalties = this.$cookies.get("loyalties");
    // console.log(doneQuests);
    // console.log(doneHideout);
    // console.log(stocked, typeof stocked);
    // console.log(config);
    // console.log(money);
    // console.log(levels);
    // console.log(loyalties);
    // JSON.parse(
    // console.log(JSON.parse(doneQuests));
    // console.log(JSON.parse(doneHideout));
    // console.log(JSON.parse(stocked));
    // console.log(JSON.parse(config));
    // console.log(JSON.parse(money));
    // console.log(JSON.parse(levels));
    // console.log(JSON.parse(loyalties));
    if (
      doneQuests ||
      doneHideout ||
      stocked ||
      config ||
      levels ||
      money ||
      loyalties
    ) {
      var state = {
        doneQuests: JSON.parse(doneQuests),
        doneHideout: JSON.parse(doneHideout),
        stocked,
        config,
        money,
        levels,
        loyalties
      };
      console.log(state);
      this.$store.replaceState(state);
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* color: #e3e3e3; */
  /* margin-top: 60px; */
}

/*
  background light 171715
  background dark 0e0e0e
  text light aeaeb0
  button background 9a8866
*/
.v-input--checkbox {
  margin: 0px;
  padding: 0px;
  height: 20px;
}
.v-treeview-node__root {
  min-height: 0px !important;
}

html,
body {
  color: #9a8866;
}
.tiny {
  font-size: 10px;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex {
  flex-grow: 1;
  flex-grow: 0;
}

.flex-grow {
  flex-grow: 1;
}

.flex-shrink {
  flex-grow: 1;
}
.h100 {
  height: 100%;
}
</style>
